/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import Layout from "Project/_components/layouts/001"
import PageCover from "Project/_components/pageCover"
import { ChevronRight, Dot } from "react-bootstrap-icons"

export default function GarantiesPage() {
  const context = useThemeUI()
  const { theme } = context

  return (
    <Layout>
      <PageCover text={"Контакты"}>
        <StaticQuery
          query={graphql`
            {
              file(relativePath: { eq: "_assets/covers/cover-004.jpg" }) {
                childImageSharp {
                  fixed(width: 1600) {
                    ...GatsbyImageSharpFixed_withWebp
                  }
                }
              }
            }
          `}
          render={data => (
            <Img
              fixed={data.file.childImageSharp.fixed}
              style={{
                display: "flex",
                minHeight: "240px",
                width: "100%",
                position: "absolute !important",
              }}
            />
          )}
        ></StaticQuery>
      </PageCover>

      <article sx={{ padding: "16px" }}>
        <div
          className={"element element-small"}
          sx={{ textTransform: "uppercase", color: "text_secondary" }}
        >
          <div className={"element-left-part"}>
            <span>Техническая поддержка</span>
          </div>
        </div>
        <div
          sx={{
            backgroundColor: "background_primary",
            boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}, 0px -1px 0px ${theme.colors.separator_opaque}, 1px 0px 0px ${theme.colors.separator_opaque}, -1px 0px 0px ${theme.colors.separator_opaque}`,
            display: "flex",
            flexDirection: "column",
          }}
        >
          <a
            href={"mailto:acksiostm@gmail.com"}
            target={"blank"}
            className={"element element-large element-row"}
            sx={{ width: "100%" }}
          >
            <div className={"element-left-part"}>
              <i>
                <Dot size={18} sx={{ color: "gray_02" }} />
              </i>
              <span sx={{ color: "tint_blue" }}>Email</span>
            </div>
          </a>
          <a
            href={"https://t.me/veroniquel3bedeva"}
            target={"blank"}
            className={"element element-large element-row"}
            sx={{ width: "100%" }}
          >
            <div className={"element-left-part"}>
              <i>
                <Dot size={18} sx={{ color: "gray_02" }} />
              </i>
              <span sx={{ color: "tint_blue" }}>Telegram</span>
            </div>
          </a>
        </div>
      </article>
    </Layout>
  )
}
