/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import { Dot, DropletFill } from "react-bootstrap-icons"
import Layout from "Project/_components/layouts/001"
import PageCover from "Project/_components/pageCover"

export default function RulesPage() {
  return (
    <Layout>
      <PageCover text={"Правила"}>
        <StaticQuery
          query={graphql`
            {
              file(relativePath: { eq: "_assets/covers/cover-007.jpg" }) {
                childImageSharp {
                  fixed(width: 1600) {
                    ...GatsbyImageSharpFixed_withWebp
                  }
                }
              }
            }
          `}
          render={data => (
            <Img
              fixed={data.file.childImageSharp.fixed}
              style={{
                display: "flex",
                minHeight: "240px",
                width: "100%",
                position: "absolute !important",
              }}
            />
          )}
        ></StaticQuery>
      </PageCover>

      <article sx={{ padding: "16px" }}>
        <h2>Покупая у нас товар, вы соглашаетесь со следующими правилами:</h2>

        <ol sx={{ li: { marginBottom: "16px", lineHeight: "24px" } }}>
          <li sx={{ color: "text_tertiary" }}>
            Проверка качества товара в течение 10 минут после покупки. Этого
            времени вполне хватает на проверку аккаунтов. В случае проблемы с
            аккаунтами - свяжитесь с нами по контактам, указанным на сайте. Мы
            всегда стараемся идти навстречу, даже если прошло больше указанного
            времени.
          </li>
          <li sx={{ color: "text_tertiary" }}>
            Принятый товар возврату и обмену не подлежит.
          </li>
          <li sx={{ color: "text_tertiary" }}>
            Мы проверяем аккаунты приватным софтом, на приватных прокси, что
            гарантирует защиту аккаунтов от бана.
          </li>
          <li sx={{ color: "text_tertiary" }}>
            Купленные аккаунты необходимо сохранить на своем ПК. В нашем
            магазине Ваши аккаунты хранятся в течение 30 дней, после чего ссылка
            для скачивания удаляется.
          </li>
          <li sx={{ color: "text_tertiary" }}>
            Незнание правил не освобождает от ответственности.
          </li>
          <li sx={{ color: "text_tertiary" }}>
            В случае обращения к нам за помощью, ответ поступает минимум в
            течение 10 минут, очень редко в течение дня.
          </li>
          <li sx={{ color: "text_tertiary" }}>
            Внимание! Проверяйте аккаунты сразу после покупки, если аккаунты
            нерабочие - пишите в <Link to={"/contacts"}>тех. поддержку</Link>{" "}
            для замены. Совершая покупку в нашем магазине, вы автоматически
            соглашаетесь и принимаете данные условия. Если Вы не согласны с
            данными условиями - воздержитесь от покупки.
          </li>
        </ol>
      </article>
    </Layout>
  )
}
