/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import Layout from "Project/_components/layouts/001"
import PageCover from "Project/_components/pageCover"

export default function HelpPage() {
  return (
    <Layout>
      <PageCover text={"Помощь"}>
        <StaticQuery
          query={graphql`
            {
              file(relativePath: { eq: "_assets/covers/cover-002.jpg" }) {
                childImageSharp {
                  fixed(width: 1600) {
                    ...GatsbyImageSharpFixed_withWebp
                  }
                }
              }
            }
          `}
          render={data => (
            <Img
              fixed={data.file.childImageSharp.fixed}
              style={{
                display: "flex",
                minHeight: "240px",
                width: "100%",
                position: "absolute !important",
              }}
            />
          )}
        ></StaticQuery>
      </PageCover>

      <article sx={{ padding: "16px" }}>
        <div>
          <h2>Как покупать?</h2>
          <ol sx={{ li: { marginBottom: "16px", lineHeight: "24px" } }}>
            <li>
              Ознакомиться с <Link to={"/rules"}>правилами магазина</Link>.
            </li>
            <li>Выбрать нужный товар.</li>
            <li>
              Прочитать описание к выбранному товару, нажав на него и перейдя на
              новую страницу.
            </li>
            <li>
              Нажать кнопку "Купить". Ввести свою почту, к которой у вас есть
              доступ.
            </li>
            <li>
              После успешной оплаты подождать от 5-ти до 10-ти минут. За это
              время автоматически будет проверена валидность, купленного вами
              аккаунта(ов). Затем доступы будут отправлены на вашу почту.
            </li>
          </ol>
        </div>
        <div>
          <h2>Что такое депозит магазина?</h2>
          <p>
            Каждый магазин в системе Deer.ee может положить на свой счет
            депозит, чтобы обеспечить безопасность средств своих клиентов.
          </p>
          <p>
            Если у вас возникнет спорная ситуация и{" "}
            <Link to={"/contacts"}>наша тех. поддержка</Link> не поможет вам, вы
            можете обратиться напрямую к администраци{" "}
            <a href={"https://rents.ws/ru/feedback/"} target={"blank"}>
              Deer.ee
            </a>
            . Если администрация решит, что в сложившейся ситуации виноват
            магазин - ваши средства будут возвращены из депозита нашего
            магазина.
          </p>
        </div>
      </article>
    </Layout>
  )
}
