import React from "react"
import { graphql } from "gatsby"
import BlogPage from "Project/pages/blog"
import ContactsPage from "Project/pages/contacts"
import DocsPage from "Project/pages/docs"
import GarantiesPage from "Project/pages/garanties"
import HelpPage from "Project/pages/help"
import Offer001 from "Project/pages/offer-001"
import PortfolioPage from "Project/pages/portfolio"
import RulesPage from "Project/pages/rules"
import SearchPage from "Project/pages/search"
import ShopCartPage from "Project/pages/shop-cart"
import ShopPage from "Project/pages/shop"

export default function StaticPage({ data }) {
  const pagePath = data.file.relativePath
    .replace("pages", "")
    .replace(".js", "")

  switch (pagePath) {
    case "/blog":
      return <BlogPage />
      break
    case "/contacts":
      return <ContactsPage />
      break
    case "/docs":
      return <DocsPage />
      break
    case "/garanties":
      return <GarantiesPage />
      break
    case "/help":
      return <HelpPage />
      break
    case "/portfolio":
      return <PortfolioPage />
      break
    case "/offer-001":
      return <Offer001 />
      break
    case "/rules":
      return <RulesPage />
      break
    case "/search":
      return <SearchPage />
      break
    case "/shop-cart":
      return <ShopCartPage />
      break
    case "/shop":
      return <ShopPage />
      break
    default:
      break
  }
}

export const query = graphql`
  query($slug: String!) {
    file(relativePath: { eq: $slug }) {
      relativePath
    }
  }
`
