/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import Layout from "Project/_components/layouts/001"
import PageCover from "Project/_components/pageCover"

export default function Offer001() {
  return (
    <Layout>
      <PageCover text={"Донат за 60% от стоимости"}>
        <StaticQuery
          query={graphql`
            {
              file(relativePath: { eq: "_assets/covers/cover-008.jpg" }) {
                childImageSharp {
                  fixed(width: 1600) {
                    ...GatsbyImageSharpFixed_withWebp
                  }
                }
              }
            }
          `}
          render={data => (
            <Img
              fixed={data.file.childImageSharp.fixed}
              style={{
                display: "flex",
                minHeight: "240px",
                width: "100%",
                position: "absolute !important",
              }}
            />
          )}
        ></StaticQuery>
      </PageCover>

      <article
        sx={{
          padding: "16px",

          ul: {
            li: {
              marginBottom: "16px",
              lineHeight: "24px",
            },
          },
        }}
      >
        <h2>Как воспользоваться услугой:</h2>
        <ul sx={{ marginBottom: "32px" }}>
          <li>
            Выбрать любой донат пакет на официальном портале World of Tanks
          </li>
          <li>
            Обратиться в <Link to={"/contacts"}>нашу тех. поддержку</Link>. В
            обращении указать следующее:
          </li>
          <ul>
            <li>ссылку на выбранный пакет</li>
            <li>сервер, на котором вы играете (RU, EU и т.д.)</li>
            <li>ваш никнейм в игре</li>
          </ul>
        </ul>
        <h2>Особенности:</h2>
        <ul>
          <li>Минимальная сумма заказа 50$</li>
          <li>
            Дополнительных скидок по этой услуге нет. Бонус коды не работают
          </li>
          <li>Срок ожидания выполнения заказа 24-36 часов</li>
          <li>
            Гарант возможен за ваш счет. Вы можете самостоятельно выбрать гарант
            сервис. Репутация гарант сервиса будет досконально проверена
          </li>
        </ul>
      </article>
    </Layout>
  )
}
